<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h4 name="header" class="modal-title mt-2 pt-1">
                                {{ $t('SyncUser') }}
                            </h4>
                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="listing py-3 px-3">
                            <loader :is-visible="isLoading"></loader>
                            <dynamic-form lang="en" ref="refUserSync" :buttons="buttons" :schema="FormSchema"></dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
export default{
    props:{
        UserSyncData:{
            type:Object
        }
    },
    data(){
        return{
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Title'),
                            name: "Title",
                            value: "",
                            placeholder: "Enter Title",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            disabled: true,
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('NewPassword'),
                            name: "NewPassword",
                            value: "",
                            placeholder: "Enter New Password",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ConfirmPassword'),
                            name: "ConfirmPassword",
                            value: "",
                            placeholder: "Enter Confirm Password",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "required",
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    
                },
                {
                    type: "submit",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close"
                }
            ],
            isLoading:false,
        }
    },
    created(){
        var vm = this;
        vm.isLoading = true;
        vm.FormSchema[0].Data[0].value = vm.UserSyncData.email_id;
        setTimeout(function(){
            vm.isLoading = false;
        },500);
    },
    methods:{
        ClosePopUp() {
            var vm = this;
            vm.$parent.CloseUserSyncPopUp();
        }
    }
}
</script>