<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list ref="tglistuser" :IsShowAction="true" :showCheckBox="true" :listType="['List']" ModuleName="User" SubModuleCode="Cmp_User"
            IdentityColumn="user_id" HeaderText="Manage Users" :ListData="GetUserListing" :HeaderData="Headers" :ViewList="ViewList" v-on:ApplyView="OnApplyView" 
            v-on:RefreshManageView="FetchUserData" :callbackfunction="FetchUserData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions" 
            :IsFilterApplied="searchCondition ? true : false" :ListDataCallBackFunction="FetchUserData"
            :LegendArray="LegendArray" :SortExp="sortExp" :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'user_type_id'">{{ data.row.user_type_id != null ?
                    data.row.user_type_id : '--' }}</template>
                <template v-if="data.column.COLUMN_NAME == 'department_id'">{{ data.row.department_id != null ?
                    data.row.department_id : '--' }}</template>
                <template v-if="data.column.COLUMN_NAME == 'designation_id'">{{ data.row.designation_id != null ?
                    data.row.designation_id : '--' }}</template>
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <td class="text-center single-action">
                        <a class="aUpdateStatus" data-toggle-tooltip="tooltip" data-original-title="User is Active"
                            v-on:click="ChangeUserStatus(data.row)" v-if="data.row.status_id == 'Active'">
                            <em class="fa fa-check text-success action-icon"></em>
                        </a>
                        <a v-else class="aUpdateStatus" data-toggle-tooltip="tooltip" data-original-title="User is Active"
                            v-on:click="ChangeUserStatus(data.row)">
                            <em class="fa fa-close text-danger action-icon"></em>
                        </a>
                    </td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'aduser_status' && ShowUserSyncStatus">
                    <td class="text-center single-action">
                        <a title="User has been synced on AD successfully" v-if="data.row.aduser_status == 1">{{ $t('UserAdSync') }}<i class="fa fa-random text-info action-icon disabled"></i></a>
                        <a v-else-if="data.row.aduser_status == 0" class="AdPassword" v-on:click="OpenToSyncUser(data.row)"
                            :title="data.row.aduser_reason" :userId="data.row.user_id" :emailId="data.row.email_id"
                            href="javascript:;"><i class="fa fa-retweet text-primary action-icon"></i></a>
                        <a v-else class="AdPassword" :title="data.row.aduser_reason" v-on:click="OpenToSyncUser(data.row)"
                            :userId="data.row.user_id" :emailId="data.row.email_id" href="javascript:;">Create vDesk
                            user</a>
                    </td>
                </template>
            </template>
        </tg-list>
        <modalSyncUser v-if="ShowUserSyncPopUp" :UserSyncData="UserSyncData"></modalSyncUser>
        <modalImportUser v-if="ShowImportUserPopUp"></modalImportUser>
        <modalAssociateLicense v-if="ShowAssociatePopUp" :GetAllAssociateData="GetAllAssociateData"></modalAssociateLicense>
        <modalUserDetails v-if="ShowUserDetails" :GetUserDetailsData="GetUserDetailsData"></modalUserDetails>
        <!-- <modalviewlayout v-if="ShowManageView" submodulecode="Cmp_User" modulename="User"
            @close="ShowManageView = false" v-bind:callbackfunction="fetchRecords" :usertype="usertype">
            <h5 slot="header" @GetViewId="GetViewId">{{ $t('Search') }}</h5>
        </modalviewlayout> -->
        <modalAssignUserBonus v-if="ShowUserAssignBonus" :GetUserAssignBonus="GetUserAssignBonus"></modalAssignUserBonus>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import Schema from "../Users/UserListingSchema";
import modalSyncUser from '../../components/Modals/modalSyncUser.vue';
import modalImportUser from '../../components/Modals/modalImportUser.vue';
import modalAssociateLicense from '../../components/Modals/modalAssociateLicense.vue';
import modalUserDetails from '../../components/Modals/modelUserDetails.vue';
//import modalviewlayout from '../../components/Common/view-layout.vue';
import modalAssignUserBonus from '../../components/Modals/modalAssignUserBonus.vue';
export default {
    components: {
        modalSyncUser, modalImportUser, modalAssociateLicense,modalUserDetails,modalAssignUserBonus
    },
    data() {
        return {
            isLoading: false,
            noRecord: false,
            leftSearchFields: Schema.leftsearchSchema,
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            PageSize: 10,
            PageNumber: 1,
            GetUserListing: [],
            Headers: [],
            TotalRecords: 0,
            TotalPages: 0,
            searchCondition: '',
            LegendArray: [
                {
                    class: 'legend-pending colorboxsquare alert-warning',
                    text: this.$t('UserAprovers')
                },
                {
                    class: 'legends_medium colorboxsquare alert-info',
                    text: this.$t('VirtualUser')
                },
                {
                    class: 'legends_profile colorboxsquare alert-danger',
                    text: this.$t('AdUserFailure')
                },
            ],
            ShowAssignBonus: false,
            ShowUserSyncStatus: false,
            ShowUserSyncPopUp: false,
            ShowImportUserPopUp: false,
            UserSyncData: {},
            ShowAssociatePopUp: false,
            GetAllAssociateData: {},
            ShowUserDetails:false,
            ViewList:[],
            ViewListId: '',
            viewId: 0,
            ShowManageView:false,
            GetUserDetailsData:[],
            ShowUserAssignBonus:false,
            GetUserAssignBonus:[]
        }
    },
    async created() {
        var vm = this;
        await vm.GetAllAddedLicensesUser();
        await vm.CheckExtraFeaturesUser();
        Schema.bindleftCommonSearchdropdown(vm, 'User', 'STATUS_NAME');
        Schema.bindleftCommonSearchdropdown(vm, 'User', 'DEPARTMENT');
        Schema.bindleftCommonSearchdropdown(vm, 'User', 'LOCATION');
        Schema.bindleftCommonSearchdropdown(vm, 'User', 'USER_TYPES');
        await vm.FetchUserData();
        await vm.GetDdlRecords_ViewList();
    },
    mounted() {
        Schema.leftsearchSchema.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },
    methods: {
        ChangeUserStatus(UserData) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmAssociateLicense"), true, (vm.$t("ConfirmAssociateLicense")), function () {
                // vm.GetPdfAndExcelApi();
            })
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "AddUser":
                    this.AddUser();
                    break;
                case "ImportUser":
                    this.ImportUser();
                    break;
                case "AssignManager":
                    this.AssignManager();
                    break;
                case 'ManageView':
                    this.ManageView();
                    break;
            }
        },
        AssignManager() {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
        },
        AddUser() {
            this.$router.push("/User/Add");
        },
        ImportUser() {
            var vm = this;
            vm.ShowImportUserPopUp = true;
        },
        CloseImportUserPopUp() {
            var vm = this;
            vm.ShowImportUserPopUp = false;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'ShowAssociateLicenses':
                    vm.ShowAssociateLicenses(item);
                    break;
                case 'ViewUserDetails':
                    vm.ViewUserDetails(item);
                    break;
                case 'AssignUserBonus':
                    vm.AssignUserBonus(item);
                    break;
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'View':
                        action.isVisible = true;
                        break;
                    case 'Assign Bonus':
                        action.isVisible = self.ShowAssignBonus;
                        break;
                    case 'Reset Password':
                        action.isVisible = true;
                        break;
                    case 'Send Crendentials':
                        action.isVisible = true;
                        break;
                    case 'Configuration':
                        action.isVisible = true;
                        break;
                    case 'Associate Licenses':
                        action.isVisible = true;
                        break;
                    case 'Induction Email':
                        action.isVisible = true;
                        break;
                    case 'Feedback Email':
                        action.isVisible = true;
                        break;
                    default:
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        clickEventOfColumn() {

        },
        ShowAssociateLicenses(data) {
            var vm = this;
            vm.GetAllAssociateData = data;
            vm.ShowAssociatePopUp = true;
        },
        ViewUserDetails(data){
            var vm = this;
            vm.ShowUserDetails = true;
            vm.GetUserDetailsData = data;
        },
        CloseUserDetails(){
            var vm = this;
            vm.GetUserDetailsData = [];
            vm.ShowUserDetails = false;
        },
        AssignUserBonus(data){
            var vm = this;
            if(JSON.parse(data.shift_id).value != null){
                vm.GetUserAssignBonus = data;
                vm.ShowUserAssignBonus = true;
            }else{
                vm.ShowAlert(vm.$t('ErrorShift'), "warning");
            }
        },
        CloseUserAssignBonus(){
            var vm = this;
            vm.GetUserAssignBonus = []
            vm.ShowUserAssignBonus = false;
        },
        CloseAssociatePopUp() {
            var vm = this;
            vm.ShowAssociatePopUp = false;
        },
        async FetchUserData() {
            debugger
            var vm = this;
            vm.isLoading = true;
            var params = `search=&searchQuery=${vm.searchCondition.trim()}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=&sortExp=&module=User&submodules=Cmp_User`
            await DataService.UserListing(params).then(response => {
                var GetReponse = JSON.parse(response.data.result);
                if (GetReponse.Data != null && GetReponse.FieldSchema.length > 0) {
                    const filteredColumnNames = ["user_name", "user_emp_id", "role_id", "user_type_id", "email_id", "designation_id", "department_id", "leaves_taken", "total_leaves", "status_id", "aduser_status"];
                    vm.Headers = GetReponse.FieldSchema.filter(x => filteredColumnNames.includes(x.COLUMN_NAME));
                    vm.Headers.forEach(hitem => {
                        hitem.settings = null;
                        if (hitem.COLUMN_NAME == 'user_name') {
                            hitem.settings = {
                                clickEvent: vm.clickEventOfColumn,
                                formatter: vm.columnDataFormatter,
                            };
                        }
                        if (hitem.COLUMN_NAME == 'user_type_id' || hitem.COLUMN_NAME == 'department_id' || hitem.COLUMN_NAME == 'designation_id' || hitem.COLUMN_NAME == 'status_id' || hitem.COLUMN_NAME == 'aduser_status') {
                            hitem.settings = { isInSlot: true }
                        }
                        var getheaders = vm.Headers.find(x => x.CustomFieldId == hitem.CustomFieldId);
                        if (getheaders.COLUMN_NAME == 'aduser_status') {
                            getheaders.DISPLAY_NAME = vm.$t('ActiveDirectoryUserSync');
                        } else if (getheaders.COLUMN_NAME == 'role_id') {
                            getheaders.DISPLAY_NAME = vm.$t('UserRole');
                        } else {
                            getheaders.DISPLAY_NAME = getheaders.DISPLAY_NAME != null ? vm.$t(getheaders.DISPLAY_NAME) : vm.$t(getheaders.COLUMN_NAME);
                        }
                    })
                    vm.GetUserListing = GetReponse.Data;
                    vm.GetUserListing.forEach(item => {
                        item.user_type_id = JSON.parse(item.user_type_id).Name
                        item.designation_id = JSON.parse(item.designation_id).Name;
                        item.department_id = JSON.parse(item.department_id).Name;
                        item.status_id = JSON.parse(item.status_id).Name;
                        item.role_id = JSON.parse(item.role_id).Name != null ? JSON.parse(item.role_id).Name : "--";
                    })
                    vm.TotalRecords = vm.GetUserListing[0].total_records;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.isLoading = false
                    vm.noRecord = false;
                } else {
                    vm.GetUserListing = [];
                    vm.noRecord = true;
                    vm.TotalRecords = 0;
                    vm.isLoading = false;
                }
            }).catch(err => {
                console.log(err);
            })
        },
        leftCommonSearch: async function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            await this.FetchUserData();
        },
        pagerMethod: async function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            await this.FetchUserData();
        },
        async GetAllAddedLicensesUser() {
            var vm = this;
            var params = `reqFrom=DETAIL`;
            DataService.GetAllAddedLicenses(params).then(response => {
                var getresponse = JSON.parse(response.data.result);
                if (getresponse != null) {
                    var checkvalid = getresponse.find(x => x.MODULE_NAME == 'HR');
                    vm.ShowAssignBonus = checkvalid != undefined ? true : false;
                }
            })
        },
        async CheckExtraFeaturesUser() {
            var vm = this;
            var params = `FeatureCode=VDESK`;
            DataService.CheckExtraFeatures(params).then(response => {
                var getresponse = response.data.msg;
                if (getresponse != null) {
                    vm.ShowUserSyncStatus = getresponse == 'TRUE' ? true : false;
                }
            }).catch(err => {
                console.log(err);
            })
        },
        OpenToSyncUser(value) {
            var vm = this;
            vm.UserSyncData = value;
            vm.ShowUserSyncPopUp = true;
        },
        CloseUserSyncPopUp() {
            var vm = this;
            vm.ShowUserSyncPopUp = false;
        },
        GetDdlRecords_ViewList: async function () {
            var vm = this;
            vm.ViewList = []
            var url = `moduleName=User&subModuleCode=Cmp_User&viewId=0&pageSize=10&pageNum=1&sortBy=&sortExp=`;
            await DataService.GetViewList(url).then(function (response) {
                if (response.data && typeof (response.data.DATA) != 'undefined') {
                    vm.ViewList = response.data.DATA;
                }
                var chkVACTIVE = 0;
                vm.ViewListId = "0";
                if (vm.ViewList == null || vm.ViewList == "") {

                }
                else {
                    vm.ViewList.forEach(function (item) {

                        if (item.IS_SLECTED_USER_ID > 0) {
                            chkVACTIVE = chkVACTIVE + 1;
                            vm.ViewListId = item.VIEW_ID;
                        }
                    });
                }
                var indx = 0;
                if (chkVACTIVE > 0) {
                    vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 0, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                }
                else {
                    if (vm.ViewList == null || vm.ViewList == "") {

                        vm.ViewList.push({ "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                    }
                    else {
                        vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });

                    }
                }
                setTimeout(() => {
                    if (vm.ViewListId > 0) {
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = vm.ViewListId;
                    } else {
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = "0";
                    }
                }, 1000);
            });
        },
        OnApplyView(viewId) {
            var vm = this;
            if (viewId == 0) {
                vm.DefaultView()
            }
            else {
                var obj = {
                    ViewId: viewId,
                    ModuleName: 'Ticketing',
                    SubmoduleCode: 'Ticketing'
                };
                var objectPostString = JSON.stringify(obj);
                DataService.ApplyView(objectPostString).then(function (response) {
                    if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("View")]), 'success', true, vm.$t('Alert'));
                            vm.fetchRecords();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                    }
                });

            }
        },
        ManageView: function () {
            var vm = this;
            vm.ShowManageView = true;
            setTimeout(function () {
                $("#modalManageView").modal('show');
            }, 1000);

        },
    }
}
</script>