<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h4 name="header" class="modal-title mt-2 pt-1">
                                {{ $t('AssignBonus') }}
                            </h4>
                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true"
                                class="fa fa-times"></em>
                            </button>
                        </div>
                        <loader :isVisible="isLoading"></loader>
                        <div class="listing py-3 px-3">
                            <dynamic-form lang="en" ref="refAssignBonus" :buttons="buttons" :schema="FormSchema"></dynamic-form>
                            <div class="listing">
                                <div class="table-responsive mb-2 mt-2" id="divPopUp" style="max-height: 500px; overflow-y: auto">
                                    <table class="table dt-responsive nowrap mt-0 LeaveBonusAssign" style="min-width:1300px">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th class="text-center">
                                                    Added By
                                                </th>
                                                <th class="text-center">
                                                    Added On
                                                </th>
                                                <th class="text-center">
                                                    No of bonus leaves(Days)
                                                </th>
                                                <th class="text-center">
                                                    Bonus Leaves Hours
                                                </th>
                                                <th class="text-center">
                                                    Description
                                                </th>
                                                <th>
                                                    Action
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                                <tr>
                                                    <td class="text-center text-danger no-record" colspan="8">
                                                        <span>No record(s) found</span>
                                                    </td>
                                                </tr>
                                        </tbody>
                                        <tbody>
                                            <tr>
                                                <td class="font-weight-bold">Total</td>
                                                <td></td>
                                                <td id="thTotalBonusLeaves" class="font-weight-bold arabicTR text-center"></td>
                                                <td id="thTotalBonusLeavesHours" class="font-weight-bold arabicTR text-center"></td>
                                                <td></td>
                                                <td></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import moment from 'moment';
import DataService from '../../services/DataService';
export default{
    props:{
        GetUserAssignBonus:{
            type:Array
        }
    },
    data(){
        return{
            isLoading:false,
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('LeaveType'),
                            name: "LeaveType",
                            value: "",
                            placeholder: "Please Select",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('SelectDays'),
                            name: "SelectDays",
                            value: "",
                            placeholder: "Please Select",
                            visibility: true,
                            config: {
                                options: [{name:1,value:1},{name:2,value:2},{name:3,value:3},{name:4,value:4},{name:5,value:5}],
                                showAddIcon: false,
                            },
                            validationRules: "",
                        },
                        {
                            astype: "DateField",
                            label: this.$t('Hours'),
                            name: "Hours",
                            value: "",
                            placeholder: "Please Select",
                            visibility: true,
                            config: {
                                mode: "time",
                                is24hr: false,
                            }
                        }
                    ]
                },
                {
                    layoutType:'single',
                    Data:[
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            }
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onClick:this.SaveUserBonus
                },
                {
                    type: "submit",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close"
                }
            ],
        }
    },
    created(){
        var vm = this;
        vm.GetLeaveData();
    },
    methods:{
        ClosePopUp(){
            var vm = this;
            vm.$parent.CloseUserAssignBonus();
        },
        GetLeaveData(){
            var vm = this;
            vm.isLoading = true;
            var params = `moduleName=User&type=LEAVES_TYPES`;
            DataService.GetDDLData(params).then(response=>{
                if(response.data.DATA.length > 0){
                    response.data.DATA.forEach(item=>[
                        vm.FormSchema[0].Data[0].config.options.push({
                            name:item.name,
                            value:item.value
                        })
                    ])
                    vm.$refs.refAssignBonus.UpdateKeyValue();
                }
                vm.isLoading = false;
            }).catch(err=>{
                console.log(err);
            })
        },
        SaveUserBonus(){
            var vm = this;
            debugger;
            vm.isLoading = true;
            const getDate = moment(vm.FormSchema[0].Data[2].value);
            const getminutes = getDate.hours() * 60 + getDate.minutes();
            var UserBonus = vm.FormSchema[0].Data[0].value + "^" + vm.FormSchema[0].Data[1].value + "^" + 0 + "^" + 0 + "^" + 0 + "^" +vm.FormSchema[1].Data[0].value;
            var params = `bonus=${UserBonus}&id=${vm.GetUserAssignBonus.user_id}&min=${getminutes}`;
            DataService.SaveBonus(params).then(response=>{
                if(response.data.status == 'Success' && response.data.code == '0'){
                    vm.ShowAlert(vm.$t("SaveBonus", 'success'), true, vm.$t('Alert'));
                }else{
                    vm.ShowAlert(vm.$t('ErrorBonus'), "danger");
                }
                vm.isLoading = false;
            }).catch(err=>{
                console.log(err);
            })
        }
    }
}
</script>
